    top: -0.5em;
}
button,
input,
select,
textarea {
    font-family: inherit;
    /* 1 */
    font-size: 100%;
    /* 2 */
    margin: 0;
    /* 3 */
}
input:focus,
select:focus,
textarea:focus,
button:focus, button:active {
    outline: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
.clearfix:after {
    visibility: hidden;
    display: block;
    content: " ";
    clear: both;
}
.clearfix {
    zoom: 1;
}
